import { Fragment } from 'react'
import { Disclosure} from '@headlessui/react'
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline'
import logo from "../img/navbarLogo.svg"
import CircleFlagsAr from "./logos/CircleFlagsAr"
import CircleFlagsEn from "./logos/CircleFlagsEn"
import Email from "./logos/FluentEmojiFlatEnvelopeWithArrow"





const handleButtonClick = () => {
  const formSection = document.querySelector("#form");
  formSection.scrollIntoView({ behavior: "smooth" });
};
const handleProyectos = () => {
  const proyectoSection = document.querySelector("#proyectos");
  proyectoSection.scrollIntoView({ behavior: "smooth" });
};

const handleSobreMi = () => {
  const sobreMiSection = document.querySelector("#sobreMi");
  sobreMiSection.scrollIntoView({ behavior: "smooth" });
};

const handleSkills = () => {
  const section = document.querySelector("#skills");
  section.scrollIntoView({ behavior: "smooth" });
};


const navigation = [
  { name: 'Inicio', href: '#', current: true },
  { name: 'Sobre Mi', current: false, onClick: handleSobreMi},
  { name: 'Proyectos',  current: false, onClick: handleProyectos },
  { name: 'Tecnologias', current: false, onClick: handleSkills },
]

function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

export default function Example() {
  return (
    <Disclosure as="nav" className="bg-slate-50 ">
      {({ open }) => (
        <>
          <div className="mx-auto max-w-7xl px-2 ">
            <div className="relative flex h-16 items-center justify-between">

              <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
                {/* Mobile menu button*/}
                <Disclosure.Button className="inline-flex items-center justify-center rounded-md p-2 text-gray-400 hover:bg-gray-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
                  <span className="sr-only">Open main menu</span>
                  {open ? (
                    <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                  ) : (
                    <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                  )}
                </Disclosure.Button>
              </div>

              <div className="flex flex-1 items-center justify-center sm:items-stretch sm:justify-start">
                <div className="flex flex-shrink-0 items-center">
                  <img
                    className="block h-8 w-auto lg:hidden "
                    src={logo}
                    alt="Your Company"
                  />
                 
                  <img
                    className="hidden h-8 w-auto lg:block"
                    src={logo}
                    alt="Your Company"
                  />
                  <span className="self-center text-black px-2 whitespace-nowrap text-xl font-semibold dark:text-white">
            LG Soria
          </span>
                </div>

                <div className="hidden sm:ml-6 sm:block">
                  <div className="flex lg:ml-56 space-x-4">
                    {navigation.map((item) => (
                      <button onClick={item.onClick}>
                        <a
                      
                      key={item.name}
                      href={item.href}
                      className={classNames(
                        item.current ? 'bg-slate-300 text-black' : 'text-gray-900 hover:bg-gray-700 hover:text-white',
                        'rounded-md px-3 py-2 text-sm font-medium'
                      )}
                      aria-current={item.current ? 'page' : undefined}
                    >
                      {item.name}
                    </a>
                      </button>
                    ))}
                  </div>
                </div>

              </div>

              <div className='flex flex-row justify-between '>
                <button className='mx-2' ><a href='portafolio/es/' ><CircleFlagsAr /></a></button>
                <button><a href='portafolio/en/'> <CircleFlagsEn /></a></button>

              </div>     

                     
              <div className="absolute inset-y-0 right-14 flex items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0">
                <button
                  className="border-none bg-blue-600 hover:bg-blue-800  p-2 px-3 rounded text-white hidden md:inline"
                  onClick={handleButtonClick}>Conectemos
                </button> 
                <button
                  className="border-none bg-blue-600   p-0.5 px-1 rounded text-white left-6 md:hidden"
                  onClick={handleButtonClick}><Email />
                </button>

    
              </div>
            </div>
          </div>

          <Disclosure.Panel className="sm:hidden">
            <div className="space-y-1 px-2 pb-3 pt-2">
              {navigation.map((item) => (
                <Disclosure.Button
                  key={item.name}
                  as="a"
                  href={item.href}
                  className={classNames(
                    item.current ? 'bg-gray-900 text-white' : 'text-gray-300 hover:bg-gray-700 hover:text-white',
                    'block rounded-md px-3 py-2 text-base font-medium'
                  )}
                  aria-current={item.current ? 'page' : undefined}
                >
                  {item.name}
                </Disclosure.Button>
              ))}
            </div>
          </Disclosure.Panel>
        </>
      )}
    </Disclosure>
  )
}
