import i18n from "../i18n/index"
import {motion} from "framer-motion"
import TextAnimation from "./TextAnimation";


const Heading = () => {
  return (
    <>
      <motion.section 
        initial= {{opacity : 0}}
        transition={{duration : 2}}
        animate={{opacity: 1}}
      className="tituloContainer bg-bottom bg-contain flex flex-row  ">
    
      
        <TextAnimation  />
        <motion.p
        initial= {{scale: 0 }}
        transition={{duration : 3, delay: 2}}
        animate={{scale: 1.3}} 
        class="text-lg font-normal ml-6  text-gray-300 lg:text-xl dark:text-gray-400">
        {i18n.t('titleDescription')}
        </motion.p>
      </motion.section>
    </>
  );
};

export default Heading;
