import { motion } from "framer-motion";

const TextAnimation = () => {
  const text = "Front-End Developer";
  return (
    <motion.div className="mb-4 ml-6 text-6xl font-extrabold text-gray-300 dark:text-white md:text-5xl lg:text-6xl gradient ">
      {text.split("").map((letter, index) => (
        <motion.span 
          key={index}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.2, delay: index * 0.1 }}
        >
          {letter}
        </motion.span>
      ))}
    </motion.div>
  );
}
export default TextAnimation;