import SkillsCarrusel from "./SkillsCarrusel";
import {motion} from "framer-motion"


const Skills = () => {
     return(
        <motion.section
        initial={{scale: 0}}
        transition={{duration: 1}}
        whileInView={{scale: 1}}
        id="skills" className="margin">
            
            <SkillsCarrusel />
        </motion.section>
     )
}

export default Skills;